body {
  font-family: 'Open Sans', sans-serif;
  max-width: 1100px;
  min-width: 370px;
  margin: auto;
  font-size: 1.2rem;
}

main {
  text-align: center;
  margin: 3em 2rem 2rem;
}

@media (prefers-color-scheme: dark) {
  body {
    background: #000b66;
  }

  h1 {
    color: white;
  }
}

@media (max-width:780px) {
  body {
    font-size: 1rem;
  }
}