

.input-form {
  border-radius: 15px 15px 0 0;
  display: grid;
  grid-template-columns: auto 20%;
  gap: 5%;
  background-color: #a4a6ff;
  padding: 2rem;
}

.task-input-field {
  border: 0px;
  border-radius: 10px;
  font-size: 1.4rem;
  word-wrap: break-word;
  max-height: 30vh;
  padding: 20px
}

.task-input-field::placeholder {
  font-size: 1.4rem;
}

.task-input-field:focus {
  min-height: 12vh
}

.add-task-button {
  background: linear-gradient(to right, #196BCA, #6433E0);
  background-color: #196BCA;
  color: #fff;
  /*  font-family: Trebuchet MS; */
  font-size: 18px;
  font-weight: 800;
  font-style: normal;
  text-decoration: none;
  padding: 14px 15px;
  border: 0px solid #000;
  border-radius: 10px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  max-height: 50px;
}

.add-task-button span {
  margin-right: 15px;
  display: block;
  width: 29px;
  height: 29px;
  background-repeat: no-repeat;
  background-size: contain;
  transform: rotate(0deg);
}

.add-task-button:hover {
  background: linear-gradient(to right, #5482d0, #7d5ee3);
  background-color: #5482d0;
}

.add-task-button:active {
  transform: scale(0.95);
}

@media (max-width:780px) {
  .input-form {
    display: grid;
    grid-template-columns: auto;
  }

  .task-input-field {
    font-size: 1.2rem;
    font-family: 'Open Sans', sans-serif;
  }

  .task-input-field::placeholder {
    font-size: 1.2rem;
  }
}