.task-list {
  background-color: #eeeefb;
  min-height: 15rem;
  padding: 20px 40px;
  border-radius: 0 0 10px 10px;
}

.task-list-empty {
  margin: 10% auto auto;
}

@media (max-width:780px) {
  .task-content {
    padding: 5px 20px;
  }

  .task-list {
    padding: 10px 10px;
  }

  .task-container {
    padding: 5px;
  }
}

.task-container {
  width: 100%;
  padding: 10px;
  display: grid;
  grid-template-columns: 20% auto 10%;
  align-items: center;
}

.task-date-created {
  text-align: center;
}

.task-content {
  word-break: break-all;
  padding: 5px 40px;
  text-align: center;
  min-width: auto;
}

.delete-task-button {
  text-align: center;
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAAhklEQVRIS2NkoDFgpLH5DMRYUAB0RD8OhyQCxRfgcyQhCwyAmvcDsQAeQxyBcgdwyaNb8J9KQQY3l+4WUMkDCGNwxQG5QYVh3qgF6HE2GkQEU/FoEI0GESIEaF4W3QfapUAwxFEVXAByDQlmbagCByA9nwRLQDXaQmzVJ6Eqk0RPYCqnuQUANmMYGSMDwB4AAAAASUVORK5CYII=");
  background-size: 20px 20px;
  height: 20px;
  background-repeat: no-repeat;
}

.task-list-headers {
  padding-bottom: 20px;
  display: grid;
  border-bottom: 1px solid #000000;
  grid-template-columns: 20% auto;
  font-size: 1.4rem;
  margin: 10px;
}

.delete-task-button {
  float: right;
}